<template>
	<div class="dispaygrid" v-loading="loading">
		<ul class="columnLayout">
			<router-link v-for="(item, index) in aclist" :key="index" :to="'/article/' + item.alias + '/' + item.id">
				<li class="Genericpaddings Horizontalspacing interval">
					<div>
						<el-image class="PostThumbnails" :src="item.thumbnail ? item.thumbnail : ''" lazy>
							<template #error>
								<div class="image-slot iconfont icon-jiazaitupian"></div>
							</template>
						</el-image>
					</div>

					<div class="ArticleList">
						<div class="Horizontalspacing minxgap ArticleTitlep">
							<div class="smallsquare bold"><span class="center">热门</span></div>
							<span class="turngreen Widthlimit">{{ item.title }}</span>
						</div>
						<span class="minfont grey Widthlimits">{{ item.description }}</span>
						<div class="articlebt minfont">
							<div class="Horizontalspacing interval">
								<div class="Horizontalspacing minixgap">
									<el-avatar class="pimgmins" :src="item.user.Avatar" :size="60">
										<div class="image-slot iconfont icon-setting-role"></div>
									</el-avatar>
									<span v-if="item.user.nickname" class="center">{{ item.user.nickname }}</span>
									<span v-else class="center">{{ item.user.username }}</span>
								</div>
								<div class="grey center">{{ item.pidname }}</div>
							</div>
							<div class="Horizontalspacing interval grey">
								<div class="Horizontalspacing minixgap">
									<span class="center">日期:</span>
									<span class="center">{{ item.Creationtime }}</span>
								</div>
								<div class="Horizontalspacing minixgap">
									<span class="center">点赞:</span>
									<span class="center">{{ item.Like.length }}</span>
								</div>
								<div class="Horizontalspacing minixgap">
									<span class="center">浏览:</span>
									<span class="center">{{ item.PageView }}</span>
								</div>
							</div>
						</div>
					</div>
				</li>
			</router-link>
		</ul>
		<el-pagination background class="centerend" :hide-on-single-page="value" :current-page="curPage" @current-change="page" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
	</div>
</template>
<script>
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	props: ['userid'],
	data() {
		return {
			loading: true,
			value: true,
			curPage: 1,
			pagesize: 10, //每页显示数量
			total: 0,

			//返回值
			offset: 'DESC',
			tosort: 'time',
			aclist: '',
		}
	},
	methods: {
		// 函数
		async actr() {
			let array = await api(['post=getarticlepage', { userid: this.userid, perPage: this.pagesize, page: this.curPage, offset: this.offset, tosort: this.tosort }])
			this.loading = false
			if (array.code == 200) {
				this.aclist = array.data
				this.total = array.massage / 1
			}
		},
		// fenye
		page(ele) {
			this.loading = true
			this.curPage = ele
			this.actr()
		},
	},
	mounted() {
		// 生命周期:
		if (this.userid) {
			this.actr()
		}
	},
	updated() {
		window.scrollTo({ behavior: 'instant', top: 0 })
	},
}
</script>

<style scoped>
.PostThumbnails .image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 200px;
	height: 150px;
	background: var(--el-fill-color-light);
	font-size: 30px;
}
.image-slot {
	font-size: 30px;
}
.Widthlimit {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 563px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.Widthlimits {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 600px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.Genericpaddings {
	padding: 20px 0px;
	background: #fff;
	word-wrap: break-word;
}
.dispaygrid {
	width: 820px;
}
.centerend {
	place-self: center end;
}
.pimgmins .image-slot {
	font-size: 8px;
}
</style>
