							<div>
								<el-skeleton-item variant="image" style="width: 100px; height: 75px" />
							</div>
							<div class="CatalogLayout space-around">
								<el-skeleton-item variant="h3" class="truncate-text" />
								<el-skeleton-item variant="h3" class="truncate-text" />
