					<div>随机标签</div>
				</div>
				<div class="iconfont icon-24gf-ellipsis center grey"></div>
			</div>
			<div class="Randomtags grey">
				<a class="tag" href="http://" target="_blank" rel="noopener noreferrer">标签</a>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
				<div class="tag">标签</div>
			</div>
		</div>
	</div>
</template>
<script>
import { dataRequest as api } from '@/api/api'
import { mapState } from 'vuex'
export default {
	name: 'HeaDer',
	computed: {
		...mapState(['logon', 'user']),
	},
	props: {
		userid: {
			default: null, // 默认值为0，如果没有传递userid，组件内部会使用0
		},
	},
	components: {
		// 组件
	},

	data() {
		return {
			// 參數
			arclist: '',
			list: '',
			info: '',
			fans: false,
		}
	},
	methods: {
		//函數
		async getdata() {
			this.ifuser()
			// 正序 還是順序
			// 根據哪個表排序 這個好像 之需要
			let array = await api(['post=getarticlepage', { perPage: 4, page: 1, offset: 'DESC', tosort: 'popularity', thumbnail: 'hasValue' }])
			if (array.code == 200) {
				array = array.data
				this.arclist = array
				for (const key in this.arclist) {
					this.arclist[key].Creationtime = this.arclist[key].Creationtime.substr(0, 10)
				}
			}
			// 无图文章
			array = await api(['post=getarticlepage', { perPage: 10, page: 1, offset: 'DESC', tosort: 'popularity', thumbnail: 'noValue' }])
			if (array.code == 200) {
				array = array.data
				this.list = array
				for (const key in this.list) {
					this.list[key].Creationtime = this.list[key].Creationtime.substr(0, 10)
				}
			}
			// 获取标签
			array = await api(['post=onfetch', { result: '*', from: 'dex_TAGS', type: 9 }])
			if (array.code == 200) {
				// console.log(array)
				// 随机取10个
			}
			// 登陆的时候判断

			if (this.userid) {
				this.iffollow()
			}
		},
		async ifuser() {
			if (this.userid) {
				// 获取用户信息
				let array = await api(['post=userinformation', this.userid])
				if (array.code == 200) {
					this.info = array.data
				}
			}
		},
		async iffollow() {
			console.log(this.userid)
			let array = await api(['post=followinterest', { toid: this.userid, fans: 1 }])

			if (array.code == 200) {
				this.fans = array.data
				return array.data
			}
		},
		//
		async follow() {
			// 提交关注和取消关注
			if (this.userid && this.user) {
				let array = await api(['post=followinterest', { toid: this.userid }])

				if (array.code == 200) {
					console.log('2')
					let arr = await this.iffollow()
					this.ifuser()
					if (arr) {
						// 已关注
						this.$message.success('已关注')
					} else {
						// 取消关注
						this.$message.error('取消关注')
					}
				} else if (array.code == 1001) {
					this.$message.error('不能关注自己')
				}

				// 这里看回调时关注成功还是取消关注
			} else {
				//
				this.$message.error('请先登录')
			}
		},
	},
	mounted() {
		this.getdata()
	},
	watch: {
		user(newValue, oldValue) {
			if (this.userid) {
				this.iffollow()
			}
		},
	},
}
</script>
<style scoped>
.image-slot {
