	font-size: 30px;
}
.image-slots {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 280px;
	height: 210px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.image-slot {
	font-size: 30px;
}
.truncate-text {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 170px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.pitruncate-text {
	white-space: nowrap; /* 防止换行 */
	overflow: hidden; /* 隐藏超出部分 */
	text-overflow: ellipsis; /* 添加省略号 */
	width: 170px; /* 设置容器宽度 */
}
.listtruncate-text {
	white-space: nowrap; /* 防止换行 */
	overflow: hidden; /* 隐藏超出部分 */
	text-overflow: ellipsis; /* 添加省略号 */
	width: 212px; /* 设置容器宽度 */
}
.el-card {
	border: 0px;
	border-radius: 0px;
}
.author {
	display: flex;
	flex-direction: column;
	gap: 20px;
}
.Roundheadportraitbig {
	width: 70px !important;
	height: 70px !important;
}
.authortree {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}
.author .el-button {
	width: 100%;
	border-radius: 4px;
}
.abstract {
	padding: 10px;
	border-radius: 4px;
}
.fans {
	width: 100%;
	height: 32px;
}
</style>
